@charset "UTF-8";
.ly-homeIco {
  text-align: center; }
  .ly-homeIco_image {
    width: 50%;
    max-width: 160px;
    margin: 0 auto 24px; }

.ly-homeProfile {
  margin-bottom: 16px; }

.swiper-container {
  margin-bottom: 24px; }
  .swiper-container .swiper-wrapper .swiper-slide_cnt {
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.08);
    margin: 10px 0;
    padding: 42px 48px 16px;
    background: #222;
    height: 240px;
    position: relative; }
  .swiper-container .swiper-wrapper .swiper-slide_tag {
    display: flex;
    list-style: none;
    padding: 0 10px;
    color: #ffffff;
    background: #8a8a8a;
    position: absolute;
    left: 0;
    top: 0; }
    .swiper-container .swiper-wrapper .swiper-slide_tag_list {
      font-size: 1.3rem;
      padding: 0 3px 3px;
      line-height: 1.2; }
      .swiper-container .swiper-wrapper .swiper-slide_tag_list:after {
        content: "/";
        font-size: 1.6rem;
        position: relative;
        top: 2px; }
      .swiper-container .swiper-wrapper .swiper-slide_tag_list:last-of-type::after {
        content: ""; }
  .swiper-container .swiper-wrapper .swiper-slide_title {
    font-size: 1.8rem;
    margin: 0 0 12px; }
  .swiper-container .swiper-wrapper .swiper-slide_lead {
    font-size: 1.6rem;
    color: #d8d8d8;
    line-height: 1.6;
    margin: 0 0 6px; }
  .swiper-container .swiper-wrapper .swiper-slide_updateDate {
    font-size: 1.3rem;
    color: #d8d8d8;
    position: absolute;
    right: 24px;
    margin: 0;
    bottom: 36px; }
  .swiper-container .swiper-button-next, .swiper-container .swiper-button-prev {
    width: 60px;
    height: 36px;
    bottom: 10px;
    top: auto;
    background: none; }
    .swiper-container .swiper-button-next:after, .swiper-container .swiper-button-prev:after {
      content: "";
      position: absolute;
      display: inline-block;
      width: 10px;
      height: 10px;
      right: 25px;
      bottom: 13px;
      border-bottom: 1px solid #d8d8d8;
      border-right: 1px solid #d8d8d8; }
    .swiper-container .swiper-button-next:focus, .swiper-container .swiper-button-prev:focus {
      outline: 0 !important; }
  .swiper-container .swiper-button-prev:after {
    transform: rotate(135deg); }
  .swiper-container .swiper-button-next:after {
    transform: rotate(-45deg); }
  .swiper-container.swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 18px; }
    .swiper-container.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
      background: #eff1f3; }
      .swiper-container.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet.swiper-pagination-bullet-active {
        background: #f76eb5; }

