.bl-categoryList {
  list-style: none;
  padding: 0;
  margin: 0;
  background: #222; }
  .bl-categoryList_cnt {
    width: 100%;
    padding: 8px 32px;
    border-bottom: 1px solid #555;
    position: relative; }
    .bl-categoryList_cnt:after {
      content: "";
      position: absolute;
      display: inline-block;
      width: 8px;
      height: 8px;
      right: 40px;
      bottom: 20px;
      border-bottom: 1px solid #888;
      border-right: 1px solid #888;
      transform: rotate(45deg);
      transition-duration: .3s; }
    .bl-categoryList_cnt.is-active:after {
      transform: rotate(-135deg); }
  .bl-categoryList_index {
    display: block;
    color: #181818; }
  .bl-categoryList_ttl {
    display: flex;
    align-items: baseline; }
    .bl-categoryList_ttl h2 {
      font-size: 1.5rem;
      font-weight: normal;
      padding-right: 6px;
      margin: 0;
      color: #fff; }
    .bl-categoryList_ttl span {
      font-size: 1.3rem;
      color: #fff;
      align-items: center; }
  .bl-categoryList_txt {
    font-size: 1.5rem;
    margin-bottom: 0; }
  .bl-categoryList_menu {
    line-height: 0;
    height: 0;
    overflow: hidden;
    opacity: 0.3;
    transition-duration: .3s;
    padding: 0 20px;
    background: #eff1f3; }
    .bl-categoryList_menu.is-open {
      line-height: normal;
      /* numberに書き換える*/
      height: auto;
      opacity: 1;
      padding: 10px 20px; }
      .bl-categoryList_menu.is-open ul li {
        padding: 10px; }
  .bl-categoryList_menuList {
    list-style: none;
    padding-left: 0; }
    .bl-categoryList_menuList li {
      padding: 0 10px;
      border-bottom: 1px solid #fff;
      transition-duration: .3s; }
      .bl-categoryList_menuList li:last-of-type {
        border-bottom: none; }
